import React from 'react'
import PropTypes from 'prop-types'

// material-ui
import Zoom from '@material-ui/core/Zoom'
import { withStyles } from '@material-ui/core/styles'

/**
 * hook that returns a trigger when scroll threshold is met
 */
import useScrollTrigger from '@material-ui/core/useScrollTrigger'

const styles = theme => ({
  root: {
    position: 'fixed',
    bottom: theme.spacing(2),
    right: theme.spacing(2)
  }
})

/**
 * Scroll to target (top)
 * @param {Object} props - component properties
 * @param {Object} props.classes - override the injected class names
 * @param {*} props.children - children components
 * @returns {*}
 * @constructor
 */
function ScrollTop({ classes, children }) {
  const trigger = useScrollTrigger({
    disableHysteresis: true,
    threshold: 100
  })

  /**
   * Scroll to target when triggered
   * @param event
   */
  const handleClick = event => {
    const anchor = (event.target.ownerDocument || document).querySelector('#back-to-top-anchor')

    if (anchor) {
      anchor.scrollIntoView({ behavior: 'smooth', block: 'center' })
    }
  }

  return (
    <Zoom in={trigger}>
      <div onClick={handleClick} role="presentation" className={classes.root}>
        {children}
      </div>
    </Zoom>
  )
}

ScrollTop.propTypes = {
  children: PropTypes.element.isRequired
}

export default withStyles(styles)(ScrollTop)
